.list_wrapper {
  margin-top: 30px;
}

.list_wrapper,
.list_wrapper_ul {
  width: 100%;
}

.list_wrapper_ul {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}

.list_wrapper_ul li {
  width: 100%;
  list-style: none;
  margin-bottom: 15px;
}

.list_wrapper_ul li:last-child {
  margin-bottom: 0;
}

.list_error {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.list_error h2 {
  padding-top: 50px;
}

.list_error_login {
  color: #ff8c00;
}

.card_login {
  font-size: 28px;
  font-weight: bold;
  background-color: transparent;
  color: #ff8c00;
  border: 0;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.card_login:hover {
  color: tomato;
}
